$blackish: rgb(15, 25, 35);
$whiteish: rgb(245, 247, 249);

$darkpurple: #a01e78;
//$darkpurple: orange;
$purple: #8c82a0;
$purplewcag: #73739b;
$pink: #dc82aa;
$red: #e60014;
$green: #008a20;
$yellow: #ffba00;

$break-s: 599px;
$break-m: 899px;
$break-l: 1199px;
$break-xl: 1440px;

$transition: all 300ms;
$border-radius: 0;

$darkborder: rgb(50, 60, 70);
$darkborder-lighter: rgb(20, 30, 40);
$lightborder: rgb(230, 232, 234);
$lighterdark-1: rgb(50, 60, 70);

$scheme: dark;

html {
  box-sizing: border-box;
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: $whiteish;
  border-color: $lightborder;
  font-display: swap;
  font-family: aktiv-grotesk, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 15px;
  line-height: 1.5;
  margin: 0 auto;
  overflow-x: hidden;
  position: relative;
  color: $blackish;

  @media (prefers-color-scheme: $scheme) {
    background-color: $blackish;
    border-color: $darkborder;
    color: $whiteish;
    fill: $whiteish;
  }
}

svg {
  fill: $blackish;

  @media (prefers-color-scheme: $scheme) {
    fill: $whiteish;
  }
}

div {
  border-color: inherit;
}

h1 {
  font-size: 21px;
  font-weight: 700;
  letter-spacing: 0.4px;
  margin: 0;
}

h2 {
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0.3px;
  margin: 0;
}

h3 {
  font-size: 15px;
  font-weight: 700;
  letter-spacing: 0.4px;
  margin: 0;
}

p {
  margin: 0;
  max-width: 650px;
}

// Padding

.p24 {
  padding: 24px;
}

// Invisibility

[invisible="false"] {
  opacity: 1;
  pointer-events: all;
}
[invisible="true"] {
  opacity: 0;
  pointer-events: none;
}

// Buttons

.buttons {
  display: flex;
  flex-flow: row wrap;
  margin: 20px 0;
  > button {
    margin-right: 20px;
  }
}

button {
  align-items: center;
  border: 0;
  border-radius: $border-radius;
  color: inherit;
  cursor: pointer;
  display: flex;
  flex-flow: row nowrap;
  font-weight: 500;
  font-family: inherit;
  font-size: inherit;
  height: 48px;
  letter-spacing: 0.3px;
  outline: 0;
  padding: 1px 24px 0 24px;
  transition: $transition;
  > span {
    + svg {
      margin-left: 24px;
    }
  }
  &.primary {
    border: 1px solid $darkpurple;
    background-color: $darkpurple;
  }
  &.ghost {
    border: 0;
    background-color: transparent;
  }
  &.secondary {
    border: 1px solid $darkpurple;
    background-color: rgba($darkpurple, 0);
    &:hover {
      background-color: rgba($darkpurple, 0.12);
    }
  }
  &.tertiary {
    box-shadow: 1px 0 0 0 $darkborder-lighter;
    background-color: rgba($blackish, 1);
    &:hover {
      background-color: rgba($darkpurple, 0.12);
    }
  }
}

// Lists - tables - grid

.table-tools {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-end;
  width: 100%;
  > button {

  }
}

.table-tools-button {
  background-color: rgba($blackish, 1);
  box-shadow: -1px 0 0 0 $darkborder-lighter, 1px 0 0 0 $darkborder-lighter;
  flex: 0 0 58px;
  &:hover {
    background-color: rgba($darkpurple, 0.12);
  }
}
.table-search {
  display: flex;
  flex-flow: row wrap;
  flex: 1 0 auto;
  justify-content: flex-end;
  position: relative;
}

.table-search-icon {
  align-items: center;
  display: flex;
  justify-content: center;
  right: 0;
  pointer-events: none;
  position: absolute;
  transition: $transition;
  height: 48px;
  width: 58px;
}

input[type="search"] {
  background-color: rgba(white, 0);
  box-shadow: -1px 0 0 0 $darkborder-lighter, 1px 0 0 0 $darkborder-lighter;
  border-radius: $border-radius;
  color: inherit;
  cursor: pointer;
  display: flex;
  flex-flow: row wrap;
  flex: 0 0 58px;
  font-family: inherit;
  font-size: 15px;
  letter-spacing: 1px;
  min-height: 44px;
  padding: 1px 0 0 0;
  transition: $transition;
  width: 0;
  &:focus, &:not(:placeholder-shown) {
    border-bottom: 1px solid $darkpurple;
    background-color: rgba(white, 0.03);
    flex: 1 0 0;
    padding: 1px 14px 0 50px;
    +.table-search-icon {
      right: calc(100% - 58px);
    }
  }
}

.table-search-open {
  z-index: 20;
}

.table-search-close {
  transform: translateX(-100%);
  z-index: 20;
}

// Form

form {
  padding: 20px;
  position: relative;
}

label {
  display: block;
  font-family: inherit;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 0.5px;
  margin-bottom: 7px;
  opacity: 0.8;
  margin-bottom: 10px;
}

input {
  border: 0;
  outline: 0;
}

input[type="text"] {
  border-bottom: 1px solid $darkborder;
  background-color: rgba(white, 0.04);
  border-radius: $border-radius;
  color: inherit;
  flex-flow: row wrap;
  font-family: inherit;
  font-size: 15px;
  letter-spacing: 1px;
  min-height: 44px;
  padding: 1px 14px 0 14px;
  transition: $transition;
  width: 250px;
  &:focus {
    border-bottom: 1px solid $darkpurple;
    background-color: rgba(white, 0.03);
  }
  + label {
    margin-top: 24px;
  }
}

input[type="radio"] {
  display: inline;
  margin: 0;
  padding: 0;
  appearance: none;
  + label {
    color: inherit;
    display: block;
    font-family: inherit;
    font-size: 15px;
    letter-spacing: 1px;
    margin: 0 0 9px 27px;
    &::before {
      background-color: transparent;
      border: 3px solid $blackish;
      box-shadow: 0 0 0 1px $darkborder;
      border-radius: 50%;
      content: "";
      display: block;
      position: absolute;
      height: 18px;
      width: 18px;
      transform: translate(-27px, 0);
    }
    + input {
      margin-top: 10px;
    }
    + button {
      margin-top: 20px;
    }
  }
  &:checked + label:before {
    background-color: $darkpurple;
    box-shadow: 0 0 0 1px $darkpurple;
  }
}

// Modal

.modal-overlay {
  background-color: rgba($whiteish, 0.05);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  z-index: 3000;
  &.zoom-out {
    cursor: zoom-out;
  }
}

.modal {
  background-color: $blackish;
  border-radius: $border-radius;
  border: 1px solid $darkborder;
}

.modal-img {
  line-height: 0;
}

// Dashboard

.counter {
  > .number {
    font-weight: 500;
    margin-right: 7px;
  }
  > .unit {
    font-weight: 400;
    opacity: 0.8;
  }
}

// Coloring

.red-text {
  color: #dc82aa;
}

.muted {
  opacity: 0.7;
}

// Icons

.icon {
  stroke: $whiteish;
}

// Padding

.pn {
  padding: 20px;
}

// Borders

.bb {
  border-bottom: 1px solid $lightborder;
  @media (prefers-color-scheme: $scheme) {
    border-bottom: 1px solid $darkborder;
  }
}

// Background

.bg {
  background-color: $whiteish;
  @media (prefers-color-scheme: $scheme) {
    background-color: $blackish;
  }
}

// Headers

.sticky-header {
  @extend .bg;
  align-items: center;
  box-shadow: 0 0 0 1px $darkborder;
  display: flex;
  flex-flow: row wrap;
  padding: 0;
  position: sticky;
  z-index: 700;
  > h1,
  h2,
  h3 {
    padding: 2px 24px 0 24px;
  }
  &.main {
    height: 60px;
    top: 0px;
  }
  &.sub {
    height: 48px;
    top: 60px;
  }
  &.subsub {
    background-color: rgb(10, 20, 30);
    height: 50px;
    top: 110px;
  }
}

// gauges

.large-gauge {
  background: conic-gradient(
    rgba($blackish, 1) 0%,
    rgba($blackish, 1) 25%,
    rgba($darkpurple, 0) 25%,
    rgba($darkpurple, 1) 80%,
    rgba(black, 0.25) 0%
  );
  border-radius: 50%;
  height: 150px;
  position: relative;
  transform: rotate(90deg);
  width: 150px;
  &:after {
    background-color: $blackish;
    border-radius: 50%;
    content: "";
    height: 120px;
    position: absolute;
    top: 15px;
    left: 15px;
    width: 120px;
  }
}

.gauge-label {
  display: block;
}

.gauge-value {
  display: block;
  font-size: 30px;
  font-weight: 500;
  letter-spacing: 1px;
}

// Boxes

.box {
  background-color: rgba($blackish, 0.04);

  @media (prefers-color-scheme: $scheme) {
    background-color: rgba($whiteish, 0.04);
  }
}

.aside-toggle {
  display: flex;
  @media (min-width: $break-l) {
    display: none;
  }
}

.app {
  background-color: inherit;
  border-color: inherit;
  display: flex;
  flex-flow: row nowrap;
  margin: 0 auto;
  max-width: 100%;
  width: $break-xl;
}

header {
  background-color: $whiteish;
  border-color: inherit;
  border-width: 1px;
  border-top-style: solid;
  color: $blackish;
  display: flex;
  flex-flow: column nowrap;
  fill: $blackish;
  flex: 0 0 auto;
  height: auto;
  overflow-y: auto;
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2000;
  > .logo {
    margin: 15px 30px 15px auto;
  }
  @media (prefers-color-scheme: $scheme) {
    background-color: $blackish;
    border-color: $darkborder;
    color: $whiteish;
    fill: $whiteish;
  }
  @media (min-width: $break-m) {
    border-top-style: none;
    border-right-style: solid;
    height: 100vh;
    position: sticky;
    top: 0;
    > .logo {
      display: block;
      margin: 30px;
    }
  }
}

.desktop-nav {
  background-color: inherit;
  display: flex;
  flex-flow: column wrap;
  flex: 1 0 auto;
  padding: 0 20px;
  position: fixed;
  bottom: 64px;
  > a {
    background-color: inherit;
    border-radius: $border-radius;
    color: inherit;
    display: flex;
    font-size: 17px;
    font-weight: 500;
    margin: 5px 0;
    padding: 12px 15px;
    text-decoration: none;
    transition: $transition;
    &:last-child {
      margin-top: auto;
      margin-bottom: 20px;
    }
    > svg {
      transition: $transition;
      transform: scale(1);
    }
    > span {
      margin-left: 20px;
      display: block;
    }
    &:hover {
      background-color: rgba($darkpurple, 0.12);
      > svg {
        transition: $transition;
      }
    }
    &.active {
      color: $darkpurple;
      > svg {
        fill: $darkpurple;
      }
    }
  }
  @media (min-width: $break-m) {
    position: relative;
    bottom: unset;
  }
}

.tab-nav {
  background-color: $blackish;
  border-color: inherit;
  border-bottom-style: solid;
  border-width: 1px;
  display: flex;
  flex-flow: row nowrap;
  position: sticky;
  top: 60px;
  > a {
    align-items: center;
    background-color: rgba($darkpurple, 0);
    border-bottom: 3px solid rgba($darkpurple, 0);
    border-right: 1px solid rgba($darkborder, 0.3);
    color: inherit;
    display: flex;
    flex-flow: row wrap;
    font-size: 16px;
    font-weight: 600;
    height: 48px;
    letter-spacing: 0.4px;
    padding: 3px 24px 0 24px;
    text-decoration: none;
    transition: $transition;
    &:hover {
      background-color: rgba($darkpurple, 0.12);
    }
    &.active {
      border-bottom: 3px solid rgba($darkpurple, 1);
    }
  }
}

.back-arrow {
  align-items: center;
  border-right: 1px solid rgba($darkborder, 0.2);
  display: flex;
  flex-flow: row wrap;
  height: 100%;
  padding: 0 24px;
  line-height: 0;
  > svg {
    fill: $darkpurple;
  }
  &:hover {
    background-color: rgba($darkpurple, 0.1);
  }
}

main {
  border-color: inherit;
  background-color: inherit;
  flex: 1 1 auto;
  padding-bottom: 58px;
  @media (min-width: $break-m) {
    flex-flow: column wrap;
    border-width: 1px;
  }
}

aside {
  background-color: inherit;
  border-color: inherit;
  border-left-style: solid;
  border-width: 1px;
  display: none;
  flex: 0 0 370px;
  > div {
    position: sticky;
    top: 0;
  }
  @media (min-width: $break-l) {
    display: block;
  }
}

// Failed registrations

.failed-registrations {
  display: flex;
  flex-flow: row wrap;
  position: sticky;
  top: 64px;
  > .list {
    border-color: inherit;
    border-right-style: solid;
    border-width: 1px;
    flex: 0 0 50%;
    position: relative;
  }
  > .item {
    flex: 0 0 50%;
    position: sticky;
    top: 100px;
  }
}

.failed-images {
  display: flex;
  flex-flow: row wrap;
  cursor: zoom-in;
}

.failed-img-overview {
  flex: 0 0 50%;
  line-height: 0;
  > img {
    width: 100%;
  }
}

.failed-img-plate {
  flex: 0 0 100%;
  line-height: 0;
  > img {
    width: 100%;
  }
}

// Lists

.list-item {
  background-color: inherit;
  border-bottom-style: solid;
  border-width: 1px;
  color: inherit;
  min-height: 48px;
}

.list-item-link {
  align-items: center;
  display: flex;
  color: inherit;
  text-decoration: none;
  display: flex;
  flex-flow: row wrap;
  padding: 14px 0 12px 0;
  &:hover {
    background-color: rgba(white, 0.04);
  }
}

.list-confidence {
  flex: 0 0 80px;
  padding: 0 20px;
}

.list-date {
  flex: 0 0 80px;
  padding: 0 20px;
}

.list-daterange {
  flex: 0 0 280px;
  padding: 0 20px;
}

.list-id {
  flex: 0 0 80px;
  opacity: 0.5;
  padding: 0 20px;
}

.list-plate {
  flex: 0 0 200px;
  padding: 0 20px;
}

.list-status {
  flex: 0 0 100px;
  padding: 0 20px;
}

.list-text {
  flex: 0 0 150px;
  padding: 0 20px;
}

.list-title {
  flex: 0 0 230px;
  padding: 0 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.list-time {
  flex: 0 0 80px;
  padding: 0 20px;
}
